* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

ul,
li {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style: none;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.flag-image {
  width: 30px;
  margin-right: 10px;

}

.country-item {
  display: flex;
  align-items: center;
  justify-self: start;
  font-size: 20px;
  padding: 10px;
}

.country-name {
  font-size: 40px;
  margin: 30px 0;
}

.item-name {
  font-weight: bold;
  margin-right: 5px;
}